	module.exports = {"data":{"markdownRemark":{"html":"<p>HackerOne offers 3 different products:</p>\n<table>\n<thead>\n<tr>\n<th>Product</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>HackerOne Response</td>\n<td>Organizations receive vulnerability reports discovered by third-party hackers, free of charge. Security reports are ensured to end up in front of infosec professionals while minimizing the chances that vulnerabilities are disclosed through unsuitable channels like social media.</td>\n</tr>\n<tr>\n<td>HackerOne Challenge</td>\n<td>A private, fully-managed alternative to traditional penetration testing. Challenges are perfect for organizations looking to supplement or replace traditional penetration tests with ethical hackers looking for severe vulnerabilities. Every Challenge will include a detailed report to help meet compliance requirements.</td>\n</tr>\n<tr>\n<td>HackerOne Bounty</td>\n<td>A program where trusted hackers are incentivized to continuously test for critical vulnerabilities. Bounty programs can be private and invite-only or fully public, and all incentives will reflect the organization’s priorities.</td>\n</tr>\n</tbody>\n</table>\n<p>In addition to the product offerings, there are also different product editions  </p>\n<ul>\n<li>HackerOne Response</li>\n<li>HackerOne Bounty</li>\n<li>HackerOne Challenge</li>\n</ul>","frontmatter":{"path":"/programs/products","title":"Products"}}},"pathContext":{}}

